#timeline .col-sm-4 {
    width: 20%;
}

#timeline .timeline-wrapper {
    border: 1px solid #e4e4e4;
    padding: 28px 20px;
    text-align: center;
    margin: 30px 0;
    border-radius: 3px;
}

#timeline .timeline-icon i {
    font-size: 50px;
}

#timeline .timeline-icon {
    margin-bottom: 10px;
}

#timeline .timeline-name {
    font-size: 15px;
    font-weight: 500;
}

#timeline .col-sm-4 {
    padding-left: 7px;
    padding-right: 7px;
}

#timeline .row {
    margin-left: -7px;
    margin-right: -7px;
}

#timeline .col-sm-4 {
    position: relative;
}

#timeline .col-sm-4:after {
    content: "\f105";
    font: normal normal normal 24px/1 FontAwesome;
    position: absolute;
    top: 50%;
    height: 30px;
    width: 30px;
    border-radius: 50%;
    text-align: center;
    padding: 1px 0;
    background: #2c9f94;
    color: #fff;
    margin-top: -15px;
    right: -14px;
    z-index: 2;
}

#timeline .col-sm-4:last-child:after {
    display: none;
}

#timeline .active .timeline-wrapper {
    position: relative;
}

#timeline .active .timeline-wrapper:after {
    content: "";
    height: 4px;
    background: #349e93;
    position: absolute;
    left: -1px;
    right: -1px;
    bottom: -2px;
    border-bottom-left-radius: 2px;
    border-bottom-right-radius: 2px;
}

@media screen and (max-width: 768px) {
  #timeline .timeline-wrapper {
      min-height: 200px;
  }
}

@media screen and (max-width: 480px) {
  #timeline .col-sm-4 {
      width: 100%;
  }

  #timeline .col-sm-4:after {
      bottom: -14px;
      top: auto;
      left: 50%;
      margin-left: -15px;
  }

  #timeline .timeline-wrapper {
      min-height: auto;
  }
}



