.idea-form {
    position: fixed;
    z-index: 100;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.67);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
    flex-direction: column;
    -ms-flex-direction: column;
    justify-content: center;
    -webkit-justify-content: center;
    -webkit-box-pack: center;
}

.idea-form-wrapper {
  max-width: 500px;
  margin: 0 auto;
  background: #fff;
  padding: 30px;
  border-radius: 3px;
  position: relative;
}

.idea-form input, .idea-form textarea {
    width: 100%;
    border: 1px solid #e4e4e4;
    height: 50px;
    margin-bottom: 13px;
    padding: 15px;
    font-size: 16px;
    outline: none;
    border-radius: 3px;
}

.idea-form textarea {
    height: 140px;
    resize: none;
}

.idea-form button {
    background: #2c9e94;
    color: #fff;
    outline: 0;
    border: 0;
    line-height: 1;
    font-size: 16px;
    font-weight: 500;
    padding: 13px 27px;
    border-radius: 3px;
}

.form-close button {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    padding: 0;
    font-size: 21px;
    position: absolute;
    right: -20px;
    top: -20px;
}



