body {
  counter-reset: my-awesome-counter;
}

.narrow-container {
  max-width: 960px;
}

.row {
    margin-left: -7px;
    margin-right: -7px;
}

.col-sm-1, .col-sm-10, .col-sm-11, .col-sm-12, .col-sm-2, .col-sm-3, .col-sm-4, .col-sm-5, .col-sm-6, .col-sm-7, .col-sm-8, .col-sm-9 {
    padding-left: 7px;
    padding-right: 7px;
}

.btn-default.focus, .btn-default:focus {
    outline: 0;
    background: #2c9f95;
    border-color: #2c9f95;
    color: #fff;
}



