.nominee-image img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}

.nominee-image {
    padding-bottom: 65%;
    position: relative;
}

.nominee-title {
    font-size: 17px;
    margin-bottom: 10px;
    font-weight: 500;
}

.nominee-description {
    line-height: 1.4;
}

.nominee-avatar img {
    width: 30px;
    height: 30px;
    border-radius: 50%;
}

.nominee-avatar > a {
    display: inline-block;
    margin-left: -4px;
}

.nominee-avatar {
    padding-left: 4px;
}

.nominee-meta .left > * {
    display: inline-block;
    margin-right: 9px;
}

.nominee-meta .left {
    float: left;
}

.nominee-meta .right {
    float: right;
}

.nominee-meta {
  overflow: hidden;
  border-top: 1px solid #e4e4e4;
  padding-top: 15px;
  margin-top: 15px;
}

.nominee-entry-date {
    color: #838383;
    font-size: 13px;
}

.nominee-wrapper {
    border: 1px solid #e4e4e4;
    padding: 15px;
    border-radius: 3px;
    position: relative;
    overflow: hidden;
}

.nominee-image {
    margin-left: -11px;
    margin-right: -11px;
    margin-top: -11px;
    margin-bottom: 10px;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    overflow: hidden;
}

.nominee-description {
    color: #626262;
}

.nominee-list .row > .col-sm-4 {
    margin-bottom: 15px;
}

#nominee-list h3 {
    margin-bottom: 20px;
}

.nominee-list .row > * {
    counter-increment: my-awesome-counter;
}

.nominee-list .nominee-wrapper:after {
  content: counter(my-awesome-counter);
  position: absolute;
  font-size: 90px;
  font-weight: 700;
  color: #f2f2f2;
  top: -5px;
  right: 10px;
  z-index: -1;
  line-height: 1;
}

.nominee-list .row > .col-sm-4:nth-child(3n+1) {
    clear: both;
}

.empty-nominee img {
    max-width: 320px;
    margin: 20px auto 0;
    display: block;
}

.empty-nominee {
    text-align: center;
    font-size: 17px;
    color: #b4b4b4;
}

@media screen and (max-width: 768px) {
  .nominee-list .row > .col-sm-4 {
      width: 50%;
  }
  .nominee-list .row > .col-sm-4:nth-child(3n+1) {
      clear: none;
  }
  .nominee-list .row > .col-sm-4:nth-child(2n+1) {
      clear: both;
  }
}

@media screen and (max-width: 480px) {
  .nominee-list .row > .col-sm-4 {
      width: 100%;
  }
  .empty-nominee img {
      max-width: 230px;
  }

  .empty-nominee > div {
      max-width: 200px;
      margin: 0 auto;
  }
  .form-close button {
      right: 10px;
  }
}



